@import "https:fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap";
.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  font-family: Roboto, sans-serif;
  display: flex;
  overflow: hidden;
}

.card__image {
  object-fit: contain;
  width: 160px;
  height: 134px;
  margin-top: 32px;
  margin-bottom: 40px;
}

.card__title {
  color: #060b35;
  text-align: left;
  margin: 0 16px 4px;
  font-size: 12px;
  font-weight: 500;
}

.card__code {
  color: #616070;
  text-align: left;
  width: 100%;
  margin-bottom: 16px;
  margin-left: 34px;
  font-size: 10px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 24px;
  padding: 0 16px;
  display: flex;
}

.card__rating .stars {
  margin-left: 16px;
}

.card__rating .card__reviews {
  color: #060b35;
  text-align: right;
  padding-right: 16px;
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.card__price .card__price-label {
  color: #616070;
  padding: 0 16px;
  font-size: 12px;
}

.card__price .card__price-value {
  color: #060b35;
  padding-right: 16px;
  font-size: 17px;
  font-weight: 700;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: calc(100% - 54px);
  padding: 12px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  transition: background-color .2s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  background: url("star.822d561e.svg") 0 0 / 16px repeat-x;
  gap: 4px;
  width: 80px;
  height: 16px;
  display: flex;
  position: relative;
}

.stars:after {
  content: "";
  background: url("star-active.edda4c43.svg") 0 0 / 16px repeat-x;
  width: 64px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

body {
  color: #060b35;
  background-color: #fff;
  width: 200px;
  height: 408px;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  display: flex;
}
